body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  min-height: 100%;
  background-color: #f2f3f3;
  font-size: 14px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul,
li {
  list-style: none;
}

a:-webkit-any-link {
  text-decoration: none !important;
}

.no-show {
  display: none !important;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

.text-center {
  text-align: center;
}

.app-loading {
  text-align: center;
  margin: 100px auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.loading {
  text-align: center;
  padding-top: 20px;
}

.side-menu {
  position: fixed;
  z-index: 99;
  background-color: #fff;
  top: 101px;
  width: 280px;
  bottom: 30px;
  .title {
    padding: 15px 0 15px 30px;
    // font-weight: bold;
    font-size: 18px;
    // line-height: 2rem;
    border-bottom: 1px solid #eaeded;
    position: relative;
    .close {
      position: absolute;
      right: 15px;
      top: 14px;
      cursor: pointer;
    }
  }
  .menu {
    padding: 10px 0 10px 30px;
    a,
    a:link {
      color: #444;
      display: block;
      padding: 8px 10px 8px 0px;
      &.active {
        font-weight: bold;
        color: #ec7211;
      }
    }
  }
}

.page-title {
  font-size: 28px;
  color: #000000;
  text-align: left;
  line-height: 36px;
  padding: 0 0 20px 0;
  position: relative;
  .back-button {
    position: absolute;
    right: 0px;
    top: -3px;
  }
}

.ipc-device-list {
  display: flex;
  flex-direction: row;
  // justify-content: space-between;
  // text-align: center;
  // flex-wrap: wrap;
  .ipc-device {
    // display: inline-block;
    width: 240px;
    padding: 10px;
    // margin: 0 auto;
    border: 1px solid #aab7b8;
    background-color: #fff;
    img {
      border: 1px solid #ddd;
    }
    .name {
      padding: 5px 0 3px 0;
      font-size: 16px;
      // font-weight: bold;
      color: #444;
    }
    &:hover {
      background-color: #f1faff;
    }
  }
}

.ipc-video-list {
  .result-info {
    font-size: 14px;
    color: #393939;
    text-align: left;
    padding: 10px 5px;
  }
  .videos {
    .video-more {
      background: #ffffff;
      box-shadow: 0 1px 2px 0 rgba(0, 28, 36, 0.3);
      padding: 10px;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      .loader {
        color: #999;
        display: block;
        width: 100%;
        text-align: center;
        padding: 10px;
      }
    }
    .ipc-video {
      width: 20%;
      padding: 5px;
      .image {
        position: relative;
        .human {
          position: absolute;
          border: 1px solid #f00;
        }
      }
      .time {
        font-size: 16px;
        color: #444;
      }
    }
  }
}

.video-replay {
  .time {
    font-size: 16px;
    color: #000000;
    text-align: left;
    line-height: 1.6em;
    padding-bottom: 10px;
    label {
      color: #444;
    }
  }
}

.search-tab {
  display: flex;
  flex-direction: row;
  border-bottom: 1px solid #d5dbdb;
  margin-top: 10px;
  .tab {
    // flex: 1;
    font-size: 14px;
    line-height: 22px;
    width: 120px;
    border-right: 1px solid #ccc;
    margin-bottom: 10px;
    // padding: 10px 20px;
    padding: 3px 10px;
    text-align: center;
    cursor: pointer;
    position: relative;
    &.active {
      font-weight: bold;
      color: #ec7211;
      &::after {
        position: absolute;
        left: 0;
        bottom: -11px;
        width: 100%;
        content: " ";
        height: 2px;
        background-color: #000;
      }
    }
  }
}

.search-tab-content {
  padding: 15px 0;
  .quick-search-list {
    display: flex;
    flex-direction: row;
    // justify-content: space-between;
    align-items: center;
    .quick-search-item {
      margin-right: 10px;
      padding: 7px 14px;
      background: #ffffff;
      border: 1px solid #545b64;
      border-radius: 2px;
      border-radius: 2px;
      cursor: pointer;
      &.active {
        color: #ec7211;
        border: 1px solid #ec7211;
      }
    }
  }
  .precise-search {
    display: flex;
    flex-wrap: wrap;
    // justify-content: space-between;
    align-items: center;
    padding: 0px 0;
  }
  .search {
    margin-top: 20px;
    // padding-top: 10px;
    // .search-btn {
    //   font-weight: bold;
    //   background: #ec7211;
    //   border: 1px solid #ec7211;
    //   border-radius: 2px;
    //   border-radius: 2px;
    //   padding: 8px 20px;
    //   color: #fff;
    //   margin-top: 10px;
    //   cursor: pointer;
    // }
  }
}

.App {
  width: 100%;
  min-height: 100%;
  .ipc-header {
    position: fixed;
    z-index: 99;
    width: 100%;
    left: 0;
    top: 0;
    height: 60px;
    background-color: #232f3e;
    color: #fff;
    display: flex;
    flex-direction: row;
    align-items: center;
    // justify-content: space-between;
    padding: 0 20px;
    .logo {
      font-size: 18px;
      font-weight: bold;
      padding-left: 20px;
      flex: 1;
      img {
        position: absolute;
        margin-left: -25px;
        margin-top: -2px;
      }
    }
    .user {
      // width: 50px;
      margin-left: 5px;
      text-align: right;
      a,
      a:link {
        color: #fff;
      }
    }
  }
  .breadcrumb {
    position: fixed;
    z-index: 99;
    height: 40px;
    line-height: 40px;
    top: 60px;
    left: 0;
    width: 100%;
    background-color: #fff;
    box-shadow: 0 1px 2px 0 rgba(0, 28, 36, 0.3);
    display: flex;
    align-items: center;

    .icon {
      width: 40px;
      text-align: center;
      padding-top: 12px;
      cursor: pointer;
    }
    .bread {
      flex: 1;
      border-left: 1px solid #d5dbdb;
      border-right: 1px solid #d5dbdb;
      padding: 0 10px;
    }
  }
  .ipc-body {
    min-height: calc(100vh - 30px);
    padding: 0px 0px 10px;

    .content {
      margin-top: 100px;
      padding: 0 15px 15px;
    }
  }
  .ipc-footer {
    height: 30px;
    background-color: #232f3e;
    color: #fff;
    // padding: 6px 15px 0 15px;
    display: flex;
    flex-direction: row;
    align-items: center;
    .item {
      font-size: 12px;
      margin-left: 15px;
      padding-left: 30px;
      font-weight: bold;
      cursor: pointer;
      .icon {
        margin-left: -25px;
        position: absolute;
        margin-top: -3px;
      }
    }
  }
}

/* Using SCSS variables to store breakpoints */
$breakpoint-tablet: 768px;
@media (max-width: $breakpoint-tablet) {
  .side-menu {
    top: 40px;
    width: 100%;
  }

  .ipc-device-list {
    display: block;
    .ipc-device {
      width: 60%;
      margin: 0 auto;
    }
  }
  .ipc-video-list {
    .videos {
      .ipc-video {
        width: 33%;
      }
    }
  }

  .search-tab-content {
    padding: 15px 0;
    .quick-search-list {
      justify-content: space-between;
    }
    .precise-search {
      display: block;
      > div {
        margin-bottom: 10px;
      }
      // justify-content: space-between;
    }
    .search {
      text-align: right;
    }
  }
  .App {
    .ipc-header {
      height: 40px;
    }
    .breadcrumb {
      height: 40px;
      top: 40px;
    }
    .ipc-body {
      min-height: calc(100vh - 30px);
      .content {
        margin-top: 80px;
      }
    }
  }
}
